// 제목 폰트
.akaya-kanadaka-regular {
  font-family: "Akaya Kanadaka", "system-ui";
  font-weight: 400;
  font-style: normal;
}
.salsa-regular {
  font-family: "Salsa", "cursive";
  font-weight: 400;
  font-style: normal;
}
.outfit-regular {
  font-family: "Outfit", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}
.outfit-semibold {
  font-family: "Outfit", sans-serif;
  font-weight: 600;
}
// 내용 폰트
@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
.gowun-dodum-regular {
  font-family: "Gowun Dodum", "sans-serif";
  font-weight: 400;
  font-style: normal;
}
.gowun-batang-regular {
  font-family: "Gowun Batang", "serif";
  font-weight: 400;
  font-style: normal;
}
.noto-serif-kr-regular {
  font-family: "Noto Serif KR", "serif";
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}
.noto-sans-kr-regular {
  font-family: "Noto Sans KR", "sans-serif";
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}


$bgColor: #FAF3EB;

.A_fadein {
  animation: AfadeIn 0.8s ease-out both;

  @keyframes AfadeIn {
  0% {
    opacity: 0;
    // filter: blur(4px);
  }
  100% {
    opacity: 1;
    // filter: blur(0);
  }
}
}

.About {
  height: calc(100vh - 196px);
  overflow-y: auto;

  &::-webkit-scrollbar {
    display: none;
  }
}

.intro {
  padding: 5% 6%;
  border-bottom: 2px solid var(--pointColor);

  > div {
    > p {
      line-height: 100%;
      font-family: "Akaya Kanadaka", "system-ui";
      font-size: 15em;
      color: $bgColor;
      text-shadow:
        -1px -1px 0 var(--pointColor),
        1px -1px 0 var(--pointColor),
        -1px 1px 0 var(--pointColor),
        1px 1px 0 var(--pointColor);

      &:nth-of-type(2) {
        width: 100%;
        text-align: right;
        color: var(--pointColor);
      }
      
    }

    .intro_text {
      padding-top: 5%;
      // padding-bottom: 3%;
      p {
        text-align: center;
        color: var(--pointColor);
        font-size: 0.9em;
        opacity: 0.7;
        font-style: italic;
      }
    }
        
  }
}

.main {
  display: flex;
  // width: 100%;
  min-height: 0;

  .side {
    padding: 20px;
    min-width: 278px;
    // height: calc(100vh - 196px);
    border-right: 2px solid var(--pointColor);

    ul {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      line-height: 25px;
      height: 100%;

      li {        
        color: var(--pointColor);

        &:first-child {
          font-family: "Akaya Kanadaka", "Salsa";
          font-size: 1.8em;
          padding-bottom: 10%;
        }

        &:not(:first-child) {
          font-size: 0.9em;

          span {
            font-weight: 600;
          }
        }
      }
    }
  }

  .content {
    flex: 1;
    // width: 100%;
    height: calc(100vh - 196px);
    min-height: 0;
    overflow-y: auto;

    &::-webkit-scrollbar {
      display: none;
    }

    .about_tit {
      text-align: center;
      width: 100%;
      line-height: 78px;
      font-family: "Akaya Kanadaka", "Salsa";
      font-size: 1.8em;
      color: var(--pointColor);
      border-bottom: 2px solid var(--pointColor);

      &:nth-of-type(3) {border-top: 2px solid var(--pointColor);}
    }

    .about_me {
      padding: 3%;
      // display: flex;
      // justify-content: center;

      h1 {
        font-family: "Akaya Kanadaka", "Salsa";
        font-size: 7em;
        font-style: italic;
        color: var(--pointColor);
        // text-shadow:
        // -1px -1px 0 var(--pointColor),
        // 1px -1px 0 var(--pointColor),
        // -1px 1px 0 var(--pointColor),
        // 1px 1px 0 var(--pointColor);
        margin-left: 8%;
        margin-top: 5%;
      }

      .my_hobbies {
        display: flex;
        flex-direction: column;
        align-items: center;
        // flex-wrap: wrap;

        .hover_txt {
          cursor: pointer;
          font-family: "Akaya Kanadaka", "Salsa";
          font-size: 8em;
          color: var(--pointColor);
          text-align: center;
          // margin-top: 2%;
          // margin-bottom: 5%;
          line-height: 250px;

          position: relative;
          z-index: 100;

          // animation: floatUpDown 1s ease-in-out infinite;
          // @keyframes floatUpDown {
          //   0%   { transform: rotate(-1deg); }
          //   50%  { transform: rotate(1deg); }
          //   100% { transform: rotate(-1deg); }
          // }

          opacity: 0.3;

          // mix-blend-mode: difference;
          transition: opacity 0.3s ease;
          &:hover {
            opacity: 1;
            // animation:none;
          }

          &:nth-child(odd) {
            margin-left: 40%;
          }

          &:nth-child(even) {
            margin-right: 50%;
          }
        }

        .hover_image {
          position: fixed;
          width: 200px;
          height: 200px;
          pointer-events: none;
          z-index: 2;
          transition: all 0.5s linear;

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
            border-radius: 20px;
          }
        }
        
      }

      .profile {
        margin-top: 1%;
        margin-bottom: 5%;
        display: flex;
        flex-direction: column;
        align-items: center;
        // justify-content: center;

        .my_img {
          fill: var(--pointColor);
          width: 350px;
          height: 350px;
        }
        
        >.my_text {
          // align-self: center;
          font-size: 1.2em;
          font-weight: 500;
          color: var(--pointColor);
          line-height: 35px;
          text-align: center;
          // width: 800px;
          // margin-left: 3%;
          // background-color: #eee;
        }
      }
      
    }

    .skills {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      // height: 400px;

      > li {
        width: 20%;
        padding: 20px;
        color: var(--pointColor);
        text-align: center;
        border-bottom: 2px solid var(--pointColor);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;

        &:not(:nth-of-type(5), :nth-of-type(10)) {
          border-right: 2px solid var(--pointColor);
        }

        &:last-child {
          // background-color: var(--pointColor);
        }

        .my_skill {
          fill: var(--pointColor);
        }

        h4 {
          font-size: 1.1em;
        }
      }
    }

    .About_bottom {
      display: flex;
      color: var(--pointColor);

      .copyright {
        padding: 2%;
        width: 55%;
        border-right: 2px solid var(--pointColor);

        p {
          font-size: 0.9em;
        }
      }

      .project_go {
        cursor: pointer;
        width: 45%;
        display: flex;
        justify-content: space-between;
        padding-top: 20px;
        padding-left: 20px;
        padding-right: 10px;
        padding-bottom: 10px;

        .my_project {
          cursor: pointer;
          font-family: "Akaya Kanadaka", "Salsa";
          font-size: 1.5em;
          font-weight: 600;
        }

        .arrow {
          cursor: pointer;
          font-size: 1.5em;
          transform: rotate(45deg);
          align-self: flex-end;
        }
        
        transition: all 0.5s ease;
        &:hover {
          background-color: var(--pointColor);
          color: $bgColor;
        }
      }
    }
  }
}