// 제목 폰트
.akaya-kanadaka-regular {
  font-family: "Akaya Kanadaka", "system-ui";
  font-weight: 400;
  font-style: normal;
}
.salsa-regular {
  font-family: "Salsa", "cursive";
  font-weight: 400;
  font-style: normal;
}
.outfit-regular {
  font-family: "Outfit", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}
.outfit-semibold {
  font-family: "Outfit", sans-serif;
  font-weight: 600;
}
// 내용 폰트
@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
.gowun-dodum-regular {
  font-family: "Gowun Dodum", "sans-serif";
  font-weight: 400;
  font-style: normal;
}
.gowun-batang-regular {
  font-family: "Gowun Batang", "serif";
  font-weight: 400;
  font-style: normal;
}
.noto-serif-kr-regular {
  font-family: "Noto Serif KR", "serif";
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}
.noto-sans-kr-regular {
  font-family: "Noto Sans KR", "sans-serif";
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}


$bgColor: #FAF3EB;
// #FAF3EB
// #FDFDFD

:root {
  --pointColor: #2B8DA8; // 기본값
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  cursor: default;
  font-family: "Gowun Batang", "serif";
}
ul,ol {list-style: none;}
a {text-decoration: none;}

body {background-color: $bgColor;}

.screen {
  position: fixed;
  top: 0;
  right: 20px;
  font-size: 0.75em;
  color: var(--pointColor);
  font-style: italic;
}
.App {
  overflow: hidden;
  width: calc(100% - 40px);
  height: calc(100vh - 40px);
  margin: 20px;
  border: 2px solid var(--pointColor);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  position: relative;
  padding-top: 152px;

  .header {
    position: fixed;
    top: 22px;
    left: 0;
    width: 100%;
    height: 110px;
  }

  .color_change {
    position: fixed;
    top: 134px;
    left: 0;
    width: 100%;
    height: 42px;
    z-index: 90;
  }

  .table {
    margin-left: 20px;
    width: calc(100% - 40px);
    // width: 1876px;
    border-spacing: 0;
    border-bottom: 2px solid var(--pointColor);

    thead {
      th {
        cursor: pointer;
        height: 110px;

        &:first-child {
          // width: 16%;
          width: 280px;
          padding: 10px;
          color: var(--pointColor);
          border-right: 2px solid var(--pointColor);
          font-size: 1.5em;
          font-family: "Salsa", "Akaya Kanadaka";
        }
        &:nth-of-type(2), &:nth-of-type(3) {
          font-size: 1.4em;
          font-family: "Akaya Kanadaka", "Salsa";
        }
        &.active {
          // width: 42.5%;
          width: 1480px;
          background-color: var(--pointColor);
          color: $bgColor;
          text-align: left;
          padding-left: 2%;
          transition: color 1s ease;
        }
        &:not(:first-child, .active) {
          // width: 42.5%;
          width: 120px;
          color: var(--pointColor);
          transition: width 0.5s linear, color 1s ease;
        }
      }
    }

    tbody {
      th {
        width: 10%;
        height: 40px;
        line-height: 40px;
        background-color: var(--pointColor);
        color: $bgColor;
        border-right: 2px solid var(--pointColor);
        font-size: 1.1em;
        font-family: "Akaya Kanadaka", "Salsa";
      }
      td {
        line-height: 38px;

        span {
          cursor: pointer;
          margin-left: 15px;
          font-size: 0.8em;

          &:first-child {color: #2B8DA8;}
          &:nth-of-type(2) {color: #4C8B61;}
          &:nth-of-type(3) {color: #E6A24A;}
          &:last-child {color: #D9665B;}
        }
      }
    }
  }
}
