// 제목 폰트
.akaya-kanadaka-regular {
  font-family: "Akaya Kanadaka", "system-ui";
  font-weight: 400;
  font-style: normal;
}

.salsa-regular {
  font-family: "Salsa", "cursive";
  font-weight: 400;
  font-style: normal;
}

// 내용 폰트
@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

.gowun-dodum-regular {
  font-family: "Gowun Dodum", "sans-serif";
  font-weight: 400;
  font-style: normal;
}

.gowun-batang-regular {
  font-family: "Gowun Batang", "serif";
  font-weight: 400;
  font-style: normal;
}

.noto-serif-kr-regular {
  font-family: "Noto Serif KR", "serif";
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.noto-sans-kr-regular {
  font-family: "Noto Sans KR", "sans-serif";
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}


$bgColor: #FAF3EB;

.P_fadein {
  animation: PfadeIn 0.8s ease-in-out both;

  @keyframes PfadeIn {
  0% {
    opacity: 0;
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
}

.Project {
  display: flex;

  .project_list {
    width: 55%;
    border-right: 2px solid var(--pointColor);
    height: calc(100vh - 196px);
    overflow-y: auto;

    &::-webkit-scrollbar {
      display: none;
    }

    .pj {
      width: 100%;
      line-height: 188px;
      padding-left: 5%;
      font-family: "Pretendard-Regular", "sans-serif";
      font-size: 3em;
      text-shadow:
        -1px -1px 0 var(--pointColor),
        1px -1px 0 var(--pointColor),
        -1px 1px 0 var(--pointColor),
        1px 1px 0 var(--pointColor);

      &:not(.on) {color: $bgColor;}

      &.on {color: var(--pointColor);}

      &:not(:last-child) {border-bottom: 2px solid var(--pointColor);}
    }
  }

  .project_inf {
    // background-color: aquamarine;
    width: 45%;
    height: calc(100vh - 196px);

    .project_img {
      width: 100%;
      height: 70%;
      border-bottom: 2px solid var(--pointColor);
      position: relative;

      &:hover .hover {
        opacity: 0;
      }
      &:hover img {
        object-position: bottom;
      }

      .hover {
        color: #444;
        font-size: 1.5em;
        font-weight: 500;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba($color: #fff, $alpha: 0.4);
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 1;
        transition: opacity 0.3s ease;
        z-index: 2;
        pointer-events: none;

        .my-icon {
          font-size: 1.2em;
        }
      }

      img {
        width: 100%;
        height: 100%;
        object-fit:cover;
        object-position: top;
        transition: object-position 3s ease-in-out;
        z-index: 1;
      }
    }

    .project_contents {
      width: 100%;
      height: 30%;
      padding: 3%;
      display: flex;

      .project_detail {
        width: 70%;

        .project_tools {
          display: flex;
          flex-wrap: wrap;
          padding: 5px 5px;

          li {
            // border: 1px solid var(--pointColor);
            border-radius: 7px;
            background-color: var(--pointColor);
            padding: 5px 13px;
            margin: 3px 5px;
            color: $bgColor;
            font-size: 0.9em;
          }
        }

        .project_tit {
          padding: 10px 10px;
          color: var(--pointColor);

          h3 {
            font-family: "Gowun Dodum", "sans-serif";
            font-size: 1.5em;
          }

          p {
            font-family: "Gowun Dodum", "sans-serif";
            font-size: 1.1em;
          }

          h4 {
            padding-top: 2%;
            // font-family: "Gowun Dodum", "sans-serif";
            font-size: 1.1em;
            font-weight: 500;
          }
        }
      }

      .project_btns {
        width: 30%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-end;

        a {
          cursor: pointer;
          margin-top: 10%;
          width: 80%;
          padding: 5% 10%;
          color: var(--pointColor);
          background-color: $bgColor;
          border: 2px solid var(--pointColor);
          border-radius: 10px;
          font-family: "Akaya Kanadaka", "system-ui";
          font-size: 150%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          transition: all 0.5s ease;

          &:hover {
            background-color: var(--pointColor);
            color: $bgColor;
          }
        }
      }

    }
  }
}